<template>
  <v-list subheader>
    <!-- <v-subheader>Step2. 1/9 ~ 1/15</v-subheader> -->

    <template v-for="([word, meaning], index) in vocaItems">

      <v-list-item :key="word" @click="playSound(index)">
        <v-list-item-avatar size="40" tile>
          <!-- <v-img :src="require(`@/assets/image/1.jpg`)"> 1 </v-img> -->
          {{index+1}}.
        </v-list-item-avatar>

        <!-- <v-list-item-content> -->
          <v-list-item-title
            v-if="isWordOn === true"
            v-text="word"></v-list-item-title>
          <v-list-item-title
            v-else></v-list-item-title>

          <v-list-item-subtitle 
            v-if="isMeaningOn === true"
            v-text="meaning"></v-list-item-subtitle>
          <v-list-item-subtitle
            v-else></v-list-item-subtitle>
        <!-- </v-list-item-content> -->

        <!-- <v-list-item-action>
          <v-btn icon @click="toggleBookmark(index)">
            <v-icon
              v-if=""
              color="grey lighten-1"
            >
              mdi-bookmark-outline
            </v-icon>
          </v-btn>
        </v-list-item-action> -->

      </v-list-item>

      <v-divider
        v-if="index < vocaItems.length - 1"
        :key="index"
        inset
      ></v-divider>

    </template>
  </v-list>
</template>

<script>

import { mapState } from 'vuex'

export default {
  name: 'Voca',
  data: () => ({
    selectedItem: null,
  }),
  methods: {
    playSound(offset) {
      try {
        const sound = require(`@/assets/audio/${this.currentTitle.toLowerCase()}/${this.baseIdx+offset+1}.mp3`);
        let audio = new Audio(sound);
        audio.play();
      } catch (error) {
        console.error(error);
      }
    }
  },
  computed: {
    ...mapState([
      'isWordOn',
      'isMeaningOn',
      'currentTitle',
      'baseIdx',
      'vocaItems',
    ])
  }
};
</script>
