const store = new Vuex.Store({
  state: {
    isWordOn: true,
    isMeaningOn: true,

    currentTitle: 'Step2',
    subtitle: '전체',
    baseIdx: 0,
    vocaItems: [
      // ...VOCA_NUMBER,
      // ...VOCA_DIRECTION,
      // ...VOCA_GREETINGS,
      // ...VOCA_SIZE,
      // ...VOCA_COLOR,
      // ...VOCA_FRUIT,
      // ...VOCA_VERB,
      ...VOCA_STEP2
    ],
  },
  mutations: {
    setIsWordOn(state, payload) {
      state.isWordOn = payload;
    },
    setIsMeaningOn(state, payload) {
      state.isMeaningOn = payload;
    },
    setVocaItems(state, [title, subtitle]) {
      state.currentTitle = title;
      state.currentSubtitle = subtitle;

      if ('Step1' === title) {
        if ('전체' === subtitle){
          state.baseIdx = 0;
          state.vocaItems = [
            ...VOCA_NUMBER,
            ...VOCA_DIRECTION,
            ...VOCA_GREETINGS,
            ...VOCA_SIZE,
            ...VOCA_COLOR,
            ...VOCA_FRUIT,
            ...VOCA_VERB,
          ];
        } else if ('숫자' === subtitle) {
          state.baseIdx = BASE_IDX_NUMBER;
          state.vocaItems = VOCA_NUMBER;
        } else if ('방향' === subtitle) {
          state.baseIdx = BASE_IDX_DIRECTION;
          state.vocaItems = VOCA_DIRECTION;
        } else if ('인사' === subtitle) {
          state.baseIdx = BASE_IDX_GREETINGS;
          state.vocaItems = VOCA_GREETINGS;
        } else if ('사이즈' === subtitle) {
          state.baseIdx = BASE_IDX_SIZE;
          state.vocaItems = VOCA_SIZE;
        } else if ('색깔' === subtitle) {
          state.baseIdx = BASE_IDX_COLOR;
          state.vocaItems = VOCA_COLOR;
        } else if ('과일 야채' === subtitle) {
          state.baseIdx = BASE_IDX_FRUIT;
          state.vocaItems = VOCA_FRUIT;
        } else if ('동사' === subtitle) {
          state.baseIdx = BASE_IDX_VERB;
          state.vocaItems = VOCA_VERB;
        } else {
          console.error("What's happening!", subtitle);
        }
      } else if ('Step2' === title) {
        if ('전체' === subtitle) {
          state.baseIdx = 0;
          state.vocaItems = VOCA_STEP2;
        } else {
          console.error("What's happening!", subtitle);
        }
      } else {
        console.error("What's happening!", title);
      }
    }
  }
})

export default store